import { Component, OnInit } from '@angular/core';
import { BookService } from '../book.service';
import { Router } from '@angular/router';
import { TagService } from '../tag.service';
import {trigger,transition, style, animate} from '@angular/animations'

@Component({
  selector: 'app-books',
  templateUrl: './books.component.html',
  styleUrls: ['./books.component.css'],
  animations : [
    trigger('fade',[
      transition('void => *' , [
        style({background : 'greenYellow' , opacity :0}),
        animate(2000)
      ]),
      transition('* => void' ,[style(
        {background : "red" , opacity : 0}
      ),
      animate(2000)
    ])
    ])
  ]
})
export class BooksComponent implements OnInit {

  books : any
  deleted;
  tags = []
  constructor(private _BookService : BookService, private router : Router,private _tagsService : TagService) { }

  HandelAdd(){
        this.router.navigate(['/addbook'])
  }
  HandleDelete(book){
   this._BookService.DeleteBookBy(book.id).subscribe(data => {this.deleted = data ;console.log(data)});
   const index =  this.books.findIndex(bok => (book == bok)) 
   this.books.splice(index,1)
  }



  HandleUpdate(id,title,description){
    this.router.navigate(['/updatebook',id,title,description])
  }

 

  ngOnInit(): void {
     this._BookService.GetAll().subscribe(data => {this.books = data ; 
      this.books = this.books[0]
     })

     
  }

}
