import { Component, OnInit } from '@angular/core';
import { TagService } from '../tag.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-tags',
  templateUrl: './tags.component.html',
  styleUrls: ['./tags.component.css']
})
export class TagsComponent implements OnInit {

  popoverTitle = 'Are You Sure !';
  popoverMessage = 'Confirm Delete ?';
  confirmClicked = false;
  cancelClicked = false;
  tag_Name : String ;
  tags : [];
  deleted: String;
  constructor(private _tagService:TagService,private router : Router) { }

  HandleTagId(tag){
    console.log(tag.id);
    this.router.navigate(['/booksbytag',tag.id])
  }

  HandleDelete(tag){
      this._tagService.DeleteTagById(tag.id).subscribe(data => {this.deleted = data ; })
      this.confirmClicked = true;
      this.ngOnInit();

  }

  HandleNewTag(){
    this.router.navigate((['/addtag']))
  }

  HandleUpdate(id, name){
    this.router.navigate(([`/updatetag`, id, name]))
  }

  ngOnInit(): void {
    this._tagService.GetTags().subscribe(data => {this.tags = data
    });
      
  }

}
